:root {
  --cellWidth: 14vmin;
}

.vierGewinntBoard {
  display: grid;
  padding: 0;
  background-color: #205bbc;
  /*grid-template-rows: 10vmin 10vmin 10vmin;*/
  grid-template-columns: var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth);
  grid-template-rows: var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth);
}

.vierGewinntWinningField {
  font-weight: bold;
}

.vierGewinntCell {
  position: relative;
  width: 100%;
  height: 100%;
}

.vierGewinntCellOverlay{

  width: 100%;
  height: 100%;
  border: 2vmin solid #2160c4;
  border-radius: 50%;
  background: white;
}

.vierGewinntWinningCell {
  border-color: white !important;
}

.vierGewinntCell0 {

}

.vierGewinntCell1 {
  position: absolute;
  top: 2vmin;
  left: 2vmin;
  right: 2vmin;
  bottom: 2vmin;
  border-radius: 100%;
  background-color: #ee1742;
}

.vierGewinntCell2{
  position: absolute;
  top: 2vmin;
  left: 2vmin;
  right: 2vmin;
  bottom: 2vmin;
  border-radius: 100%;
  background-color: #ffd324;
}
