:root {
  --cellWidth: 20vmin;
}

.memoryBoard {
  display: grid;
  padding: 0;
  /*grid-template-rows: 10vmin 10vmin 10vmin;*/
  grid-template-columns: var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth);
  grid-template-rows: var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth) var(--cellWidth);
}

.memoryCardInner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  transform: rotateY(180deg);
}

.flipped {
  transform: rotateY(0deg);
}

.memoryCard {
  transform-style: preserve-3d;
  transition: background-color 0.8s;
  position: absolute;
  top: 0;
  left:0;
  right: 0;
  bottom: 0;
  box-sizing: border-box;
  border-radius: 1em;
  background-color: white;
  padding: 0.5em;
  box-shadow: 0em 0em 0.5em rgba(0,0,0,0.5);
  backface-visibility: hidden;
}

.memoryCardBack {
  transform-style: preserve-3d;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left:0;
  right: 0;
  bottom: 0;
  border-radius: 1em;
  background-color: blue;
  padding: 0.5em;
  box-shadow: 0em 0em 0.5em rgba(0,0,0,0.5);
  transform: rotateY(180deg);
  backface-visibility: hidden;
}

.memoryCell {
  box-sizing: border-box;
  padding: 0.5em;
  position: relative;
  border-radius: 1em;
  perspective: 100vmin;

  width: 100%;
  height: 100%;
}

.playerStatusBar{
  display: flex;
  width: 100%;
}

.playerStatus:first-child{
  margin-left: 0;
}

.playerStatus:last-child{
  margin-right: 0;
}

.currentPlayer {
  transform: scale(1,1) !important;
}

@keyframes pulse-animation {
  0% {
    box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.2);
  }
  100% {
    box-shadow: 0 0 0 20px rgba(0, 0, 0, 0);
  }
}

.playerStatus{
  transform: scale(0.9,0.9);
  flex-grow: 1;
  text-align: center;
  border: 1em solid black;
  border-radius: 1em;
  margin-left: 0.5em;
  margin-right: 0.5em;

  transition: transform 0.8s;
}

